* {
  padding: 0;
  margin: 0;
}

body {
  background-color: #e9dbc5;
}

.app-title {
  text-align: center;
  margin-top: 2%;
  margin-bottom: 2%;
}

.container {
  margin-top: 2%;
}

.container p {
  font-size: 24px;
  text-align: center;
}

.container {
  width: 50%;
  border: 5px solid #aeb797;
  border-radius: 15px;
  margin: auto;
  padding: 2%;
}

.btn {
  margin: 10px;
  color: black;
  border-radius: 5px;
}

.add-btn{
  background: rgb(186, 190, 194);
  width: 100px;
  height: 40px;
}

.card {
  width: 200px;
  margin: 10px auto;
  padding: 20px;
  background-color: #aeb797;
  border: #c17d4c 3px solid;
  color: black;
  transition: 1s;
  border-radius: 5px;
}

.overturned {
  background-color: #c17d4c;
  border: #aeb797 3px solid;
  transition: 1s;
  color: white;
}

input {
  margin: 10px;
  border-radius: 5px;
  width: 170px;
  height: 40px;
  padding-left: 15px;
}

.content {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.one {
  width: 200px;
}